@import url('https://fonts.googleapis.com/css2?family=Lexend:wght@400;600;700&display=swap');
:root {
  --color-main: #46c5e4;
  --color-text: #747474;
  --color-dark: #1f2124;
  --color-gray: #677;
  --color-bg: #adeeed;
  --color-light: #acb4bd;
  --color-lighter: #f9f9f9;
  --color-white: #fff;
  --color-border:#e0e3e6;
}

.dark {
  --color-main: #71d4eb;
  --color-text: #ff8787;
  --color-dark: #f5f6f7;
  --color-gray: #999;
  --color-bg: #404040;
  --color-lighter: #292a2c;
  --color-white: #2e3235;
  --color-border:#6f85c8;
}



*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Lexend', sans-serif;
    color: inherit;
    font-size: inherit;
    scroll-behavior: smooth;
  }
  
  
  body{
    line-height: 1.8em;
    font-weight: 400;
    font-size: 16px;
  }
  
  a {
    text-decoration: none;
  }
  
  
  
  .container {
    width: 100%;
    height: 90vh;
    color: var(--color-text);
    background-color: var(--color-bg);
    display: flex;
    align-items: center;
  }
  
  
  .app {
    width: 100%;
    max-width: 480px;
    height: 88vh;
    margin: 0 auto;
    background-color: var(--color-white);
    box-shadow: 1px 1px 6px rgba(0, 0, 0, 0.05);
    position: relative;
  }
  
  
  .app-header {
    display: flex;
    align-items: center;
    padding: 16px;
    justify-content: space-between;
    background-color: var(--color-lighter);
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.1);
  }
  
  .app-header h1 {
    font-size: 30px;
    color: var(--color-dark);
    font-weight: 800;
    text-align: center;
  }
  
  .app-header button {
    border: 0;
    background: transparent;
    cursor: pointer;
  }
  
  .app-header button > svg {
    fill: var(--color-dark);
    height: 25px;
    width: 25px;
    object-fit: cover;
  }
  
  .app-body {
    padding: 16px;
  }
  
  
  
  .notes-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 16px;
  }
  
  .notes-title,
  .notes-count {
    color: var(--color-main);
    font-size: 24px;
    font-weight: 600;
  }
  
  .notes-count {
    font-size: 18px;
    color: var(--color-gray);
  }
  
  
  .notes-list {
    padding: 0;
    margin: 16px 0;
    height: 70vh;
    overflow-y: auto;
    scrollbar-width: none; 
  }
  
  .notes-list::-webkit-scrollbar {
    display: none;
  }
  
  .notes-list-item {
    border-bottom: 1px solid  var(--color-border);
    margin-bottom: 12px;
    padding: 8px 16px;
    transition: all 0.2s ease-in-out;
  }
  
  .notes-list-item:hover {
    background-color: var(--color-bg);
    cursor: pointer;
  }
  
  .notes-list-item h3,
  .notes-list-item p span {
    font-weight: 600;
  }
  
  
  .notes-list-item p span {
    color: var(--color-gray);
    display: inline-block;
    margin-right: 8px;
  }
  
  .notes-list-item p {
    font-size: 14px;
    color: var(--color-light);
  }
  
  .floating-button {
    font-size: 48px;
    position: absolute;
    bottom: 24px;
    right: 16px;
    background: var(--color-main);
    border: none;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.2);
  }
  
  
  .floating-button > svg {
    fill: var(--color-bg);
  }

  .note-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: var(--color-main);
    padding: 10px;
  }
  
  .note-header h3 {
    display: flex;
    align-items: center;
    font-size: 24px;
    cursor: pointer;
  }
  
  .note-header h3 svg {
    fill: var(--color-main);
    width: 20px;
    margin-right: 8px;
  }
  
  .note-header button {
    border: none;
    outline: none;
    font-weight: 600;
    background-color: transparent;
    font-size: 18px;
    cursor: pointer;
  }
  
  .note textarea {
    background-color: var(--color-white);
    border: none;
    padding: 16px 12px;
    width: 100%;
    height: 70vh;
    resize: none;
    scrollbar-width: none;
  }
  
  .note textarea:active,
  .note textarea:focus {
    outline: none;
    border: none;
  }
  
  .note textarea::-webkit-scrollbar {
    display: none;
  }

  .nav{
    display: flex;
    align-items: center;
    justify-content: center;
    height:10vh;
    background-color: var(--color-gray);
    color: #ff8787;
  }
  .nav-header{
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
  }
  .nav-items{
    display: flex;
    float: right;
    padding:10px;
    margin: 7px;
    
  }
  .form-signin {
    width: 100%;
    max-width: 330px;
    padding: 15px;
    margin: auto;
    align-items: center;
    justify-content: center;
  }
 
